* {
  font-family: "Poppins";
}
.cart-list img {
  width: 100px;
  height: 100px;
}
.cart-header {
  text-align: center;
  text-transform: uppercase;
  color: rgb(255, 196, 0);
}
.cart-container {
  padding: 5px;
}
.cart-list {
  display: flex;
  background-color: white;
  align-items: center;
  text-transform: uppercase;
}
.cart-list ul h5,
.cart-list ul h6 {
  margin: 0;
}
.cart-list ul {
  flex: 1;
  display: flex;
  color: red;
  text-align: center;
  justify-content: space-around;
}

.cart-list ul li {
  color: red;
}

@media (max-width: 500px) {
  .cart-list img {
    width: 40px;
    height: 40px;
  }
  .cart-header {
    text-align: center;
    text-transform: uppercase;
    color: rgb(255, 196, 0);
  }
  .cart-container {
    padding: 3px;
    font-size: 12px;
  }
  .cart-list {
    display: flex;
    background-color: white;
    align-items: center;
    text-transform: uppercase;
  }
  .cart-list ul h6,
  .cart-list ul h6 {
    margin: 0;
  }
  .cart-list ul {
    flex: 1;
    display: flex;
    align-items: center;
    color: red;
    text-align: center;
    justify-content: space-around;
  }

  .cart-list ul li {
    color: red;
  }
}

@media (max-width: 300px) {
  .cart-container {
    padding: 3px;
    font-size: 10px;
  }
}
