@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400&display=swap");

/* 
html {
  background-color: black;
} */

.search-container input::placeholder {
  opacity: 0.4;
}
.slide-up {
  transform: translateX(25%);
}

.slide-back {
  transform: translateX(200%);
}
body {
  background-color: rgb(176, 5, 5);
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

.side-menu li {
  margin-bottom: 10px;
}

.side-menu {
  display: none;
}

.slogan {
  font-family: "Poppins";
  color: rgb(255, 196, 0);
  font-weight: bold;
  font-size: 18px;
}
.slogan,
.small-logo {
  margin: 0;
}

.main .symbol {
  width: 300px;
  height: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -2;
  border-radius: 40px;
}

.main .percent {
  max-width: 80%;
}
.main .absolute {
  position: absolute;
  color: rgb(176, 5, 5);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.order-now {
  background-color: rgb(218, 20, 20);
  color: white;
  border: none;
  border-radius: 30px;
}

.order-now i {
  display: inline-block;
  color: white;
}

.main::after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  display: table;
  width: 300px;
  height: 300px;
  background-color: rgb(218, 20, 20);
  opacity: 0.9;
  border-radius: 40px;
  z-index: -1;
  overflow: hidden;
}
li .btn {
  cursor: pointer;
  border: none;
  color: rgb(255, 196, 0);
  background-color: transparent;
  padding: 2px 8px;
  cursor: pointer;
  font-size: 11px;
}

.search {
  width: 150px;
  height: 20px;
  border: none;
  border-radius: 30px;
  outline: none;
  background-color: white;
  padding: 0px 10px;
}

.notify i {
  color: rgb(255, 196, 0);
}

.notify {
  gap: 10px;
}
.header-img {
  width: 400px;
  height: 300px;
  border-radius: 30px;
}

.hold-burger-img {
  width: 50vw;
  height: 30vh;
}

.header-bar {
  display: flex;
  align-items: flex-start;
  text-align: center;
  justify-content: space-between;
  background-color: rgb(176, 5, 5);
  padding: 0px 15px;
  margin: 0;
}

.search-container,
.notify {
  display: flex;
  align-items: center;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 50px;
}
.site-nav {
  display: flex;
  list-style: none;
  font-family: "Poppins";
  background-color: rgb(176, 5, 5);
  color: rgb(255, 196, 0);
  gap: 10px;
  transition: 0.3s;
}

.site-nav a {
  color: rgb(255, 196, 0);
  text-decoration: none;
}

.small-logo {
  width: 100px;
  height: 100px;
  flex: 1;
}

.header__image-container {
  position: relative;
  display: flex;
  font-family: "Poppins";
  justify-content: space-around;
  padding: 20px;
}

.header__image-container p,
.welcome {
  color: rgb(255, 196, 0);
  margin: 0;
}

.welcome {
  font-size: 60px;
  color: rgb(255, 196, 0);
}
.header__image-container .logo3 h2 {
  margin: 0;
}

.main {
  position: relative;
  z-index: -1;
  margin-top: 80px;
  font-family: "Poppins";
  background-color: rgb(176, 5, 5);
  text-align: center;
  color: rgb(255, 196, 0);
  font-size: 20px;
}

.order-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  font-family: "Poppins";
}

.order-container {
  position: relative;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 10px;
  margin: 50px;
  width: 230px;
  height: 160px;
  background-color: transparent;
  border-radius: 50%;
}

.order-details {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  flex-direction: column;
}
.order-img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.4s;
}

.order-info {
  position: absolute;
  display: none;
  opacity: 0;
  color: rgb(255, 251, 0);
  background-color: rgba(0, 0, 0, 0.64);
  width: 150px;
  height: 120px;
  padding: 10px;
  transition: 0.4s;
  font-size: 12px;
  border-radius: 10px;
  word-spacing: 1px;
  cursor: pointer;
}
.order-container:hover .order-img {
  transform: scale(1.1);
}

.order-container:hover .order-info {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
  opacity: 1;
}
.order-container:hover .order-icon {
  top: 0px;
  right: 5px;
}

.order-amount {
  display: flex;
  margin-top: 5px;
  gap: 3px;
}

.order-amount input {
  width: 80px;
  text-align: center;
  outline: none;
}

.order-amount button {
  cursor: pointer;
}

.order-amount button:active {
  transform: scale(0.94);
}

.order-amount input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.tag {
  background-color: rgb(176, 5, 5);
  font-weight: bold;
  letter-spacing: 1px;
  padding: 5px;
  color: rgb(255, 196, 0);
  font-size: 18px;
}

.cart-button {
  margin: 3px;
  font-size: 12px;
  background-color: green;
  color: rgb(255, 196, 0);
  border: none;
  cursor: pointer;
}

.cart-button:active {
  transform: scale(0.96);
}
.price-tag {
  color: rgb(176, 5, 5);
  background-color: rgb(255, 196, 0);
  width: 50px;
  font-weight: bold;
  border-radius: 30px;
  padding: 0px 10px;
}

.tag,
.price-tag {
  margin: 0;
}

.footer-container {
  background-color: rgb(218, 20, 20);
  font-family: "Poppins";
  font-size: 10px;
}
.contact {
  color: white;
  text-align: center;
}
.footer {
  display: flex;
  justify-content: space-around;
  margin-top: 100px;
  color: white;
  height: 200px;
}

ul {
  list-style: none;
}

.footer div {
  text-align: center;
  margin-top: 30px;
}
.footer li {
  transform: translate(-25%);
}
.footer p {
  color: rgb(255, 196, 0);
}
.footer-img {
  width: 50px;
  height: 20px;
}

.fa-solid {
  cursor: pointer;
  color: rgb(255, 196, 0);
}
.fa-burger,
.fa-xmark {
  display: none;
}
.fa-bell,
.fa-cart-shopping {
  display: block;
  cursor: pointer;
}

visible {
  display: block;
}

@media (max-width: 700px) {
  .absolute {
    font-size: 15px;
  }
  .side-menu {
    font-family: "Poppins";
    position: absolute;
    top: 19px;
    right: 0px;
    background-color: transparent;
    color: rgb(255, 196, 0);
    font-size: 11px;
    width: 150px;
    max-height: 100%;
    position: absolute;
    transition: 0.5s;
    text-align: left;
    display: block;
    z-index: 10;
    padding: 10px;
  }

  .main {
    margin-top: 40px;
  }
  .side-menu a {
    color: rgb(255, 196, 0);
    text-decoration: none;
  }
  .side-menu li {
    cursor: pointer;
  }

  .order-img {
    width: 100px;
    height: 100px;
    border-radius: 50%;

    cursor: pointer;
  }
  .order-info {
    width: 120px;
    height: 100px;
    padding: 10px;
    transition: 0.4s;
    font-size: 10px;
    border-radius: 5px;
    word-spacing: 0.5px;
    cursor: pointer;
  }
  .order-container:hover .order-info {
    top: 30%;
    left: 50%;
    display: block;
    opacity: 1;
  }
  .order-icon {
    top: 10px;
    right: 60px;
  }
  .order-container:hover .order-icon {
    top: -20px;
    right: 25px;
  }

  .order-container {
    margin: 25px;
  }
  .header__image-container {
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
  }
  .welcome {
    font-size: 50px;
    color: rgb(255, 196, 0);
  }
  .small-logo {
    width: 80px;
    height: 80px;
  }
  .slogan {
    font-family: "Poppins";
    color: rgb(255, 196, 0);
    font-weight: bold;
    font-size: 15px;
  }
  .order-icon {
    width: 20px;
    height: 20px;
  }
  .tag,
  .price-tag {
    font-size: 18px;
  }

  .amount {
    font-size: 11px;
  }

  .order-amount input {
    width: 50px;
  }

  .search {
    display: none;
  }
  .footer {
    margin-top: 50px;
  }

  .main img {
    width: 300px;
    height: 300px;
  }

  .main::after {
    width: 300px;
    height: 300px;
  }

  .fa-burger,
  .fa-xmark {
    display: block;
    transition: 0.4s;
  }

  .site-nav {
    display: none;
  }
}
@media (max-width: 300px) {
  .order-img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    cursor: pointer;
  }
  .order-info {
    width: 120px;
    height: 100px;
    padding: 10px;
    transition: 0.4s;
    font-size: 10px;
    border-radius: 5px;
    word-spacing: 0.5px;
    cursor: pointer;
  }
  .order-container:hover .order-info {
    top: 30%;
    left: 50%;
    display: block;
    opacity: 1;
  }
  .order-icon {
    top: 10px;
    right: 60px;
  }
  .order-container:hover .order-icon {
    top: -20px;
    right: 25px;
  }

  .order-container {
    margin: 15px;
  }
  .header__image-container {
    display: block;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
  }
  .welcome {
    font-size: 25px;
    color: rgb(255, 196, 0);
  }
  .small-logo {
    width: 50px;
    height: 50px;
  }
  .slogan {
    font-family: "Poppins";
    color: rgb(255, 196, 0);
    font-weight: bold;
    font-size: 10px;
  }
  .order-icon {
    width: 15px;
    height: 15px;
  }
  .tag,
  .price-tag {
    font-size: 10px;
  }

  .amount {
    font-size: 10px;
  }

  .order-amount input {
    width: 30px;
  }

  .search {
    display: none;
  }
  .footer {
    margin-top: 30px;
  }

  .main img {
    width: 100px;
    height: 100px;
  }

  .main::after {
    width: 100px;
    height: 100px;
  }

  .fa-burger,
  .fa-xmark {
    display: block;
    transition: 0.4s;
  }
}
